@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/layout.scss';
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/vars/breakpoints.scss';
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/utils/theme-utils.scss';

.dd-switch-bump {
    $this: &;

    text-align: left;

    &__container {
        .eureka-callout__inner {
            padding: 0;
            text-align: left;
        }

        &__img-container {
            padding-top: #{eureka-to-rem(
                    eureka-light-theme('core-system-scale') * 14
                )};
        }
    }

    &__imgs-container {
        span {
            width: 56px;
            height: 56px;
            border-radius: 50%;
            background-color: var(
                --eureka-core-color-canvas,
                eureka-light-theme('core-color-canvas')
            );
            display: inline-flex;
            justify-content: center;
            align-items: center;
        }

        img {
            width: 35px;
            height: 35px;
        }

        #{$this}__img-atomic {
            margin-left: -10px;
        }
    }

    &__title {
        padding: #{eureka-to-rem(eureka-light-theme('core-system-scale') * 8)} 0
            #{eureka-to-rem(eureka-light-theme('core-system-scale') * 8)} 0;
    }

    &__subtitle {
        font-size: var(
            --eureka-core-typography-large-font-size,
            #{eureka-light-theme('core-typography-large-font-size')}
        );
        font-weight: bolder;
    }

    &__bump_content {
        padding-top: #{eureka-to-rem(
                eureka-light-theme('core-system-scale') * 2
            )};

        p {
            @include eureka-flex-row;

            img {
                padding-left: #{eureka-to-rem(
                        eureka-light-theme('core-system-scale') * 1
                    )};

                padding-right: #{eureka-to-rem(
                        eureka-light-theme('core-system-scale') * 4
                    )};
            }
        }
    }

    &__actions-container {
        @include eureka-flex-row;
        justify-content: center;
        margin: #{eureka-to-rem(eureka-light-theme('core-system-scale') * 8)} 0 #{eureka-to-rem(
                eureka-light-theme('core-system-scale') * 6
            )} 0;
    }

    &__note {
        font-size: var(
            --eureka-core-typography-footnote-font-size,
            #{eureka-light-theme('core-typography-footnote-font-size')}
        );
    }
}
