// ========================================================
// Dependencies
// ========================================================
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/utils/map-utils.scss';
@import '../mixins/typography.scss';
@import '../utils/theme-utils.scss';

html {
    font-size: 62.5%;
}

body {
    @include eureka-line-height;
    color: var(--eureka-core-color-text, eureka-light-theme('core-color-text'));
    font-family: var(--eureka-core-typography-font-family, eureka-light-theme('core-typography-font-family')), var(--eureka-core-typography-font-family-fallback, eureka-light-theme('core-typography-font-family-fallback'));
    font-size: var(--eureka-core-typography-font-size, eureka-light-theme('core-typography-font-size'));
    -webkit-font-smoothing: antialiased;
}

html,
body {
    background-color: transparent;
    padding: 0;
    margin: 0;
}

strong {
    font-weight: bold;
}
