@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/layout.scss';
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/vars/breakpoints.scss';
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/utils/theme-utils.scss';

.dd-switch-dd-form {
    $this: &;

    &__form-container {
        @include eureka-flex-row;
        justify-content: center;

        width: auto;
        max-width: 432px;
        margin: #{eureka-to-rem(eureka-light-theme('core-system-scale') * 6)} auto;

        img {
            width: auto;
            height: auto;
            max-width: 100%;
        }
    }

    @include eureka-breakpoint-max-only($eureka-s-min) {
        &__form-container {
            max-width: 247px;
        }
    }

    &__actions-container {
        @include eureka-flex-row;
        justify-content: center;
        margin-top: #{eureka-to-rem(
                eureka-light-theme('core-system-scale') * 20
            )};
    }
}
